body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: #777;
  background-color: #f3f3f3; }

/*
0 - 600px:      Phone
600 - 900px:    Tablet portrait
900 - 1200px:   Tablet landscape
[1200 - 1800] is where our normal styles apply
1800px + :      Big desktop

$breakpoint arguement choices:
- phone
- tab-port
- tab-land
- big-desktop

ORDER: Base + typography > general layout + grid > page layout > components

1em = 16px
*/
*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

/* =================================
  Element Styles
==================================== */
.App {
  width: 100%;
  max-width: 75rem;
  min-width: 25rem;
  margin: 2em auto;
  overflow: hidden;
  border: 1px solid #e3e3e3;
  background-color: #fff; }

.AppLogin {
  width: 100%;
  min-width: 25rem;
  max-width: 37.5rem;
  margin: 10em auto;
  overflow: hidden;
  border: 1px solid #e3e3e3;
  background-color: #fff; }

/* pending */
.pending {
  border-color: rgba(210, 210, 210, 0.5); }

.pending span {
  color: #d2d2d2; }

/* responded */
.responded {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  border-color: rgba(88, 183, 205, 0.9); }

.responded label {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  color: #58b7cd; }

.confirmed {
  margin-left: 10px; }

div > label {
  margin-top: 12px; }

h1 {
  font-size: 5.8em; }

h2 {
  font-size: 1.95em; }

form {
  margin: 1em auto 0;
  position: relative; }

.wrapper {
  width: 90%; }

.main {
  padding: 1em;
  position: relative; }

.options {
  display: inline-block;
  width: 100%; }

.filter {
  display: inline-block; }

td:last-child {
  padding-left: 5px; }

.main > div {
  margin-top: 2em; }

.login {
  border: 1px solid #e3e3e3;
  padding: 1rem;
  text-align: center;
  background-blend-mode: multiply;
  background-size: cover; }

.login input {
  float: left;
  padding: 12px;
  font-size: 1.15em;
  border: 1px solid #d5d5d5;
  width: 100%; }

@media only screen and (max-width: 60em) {
  .login .field {
    height: 5em; } }

.login .error {
  color: #A50000;
  padding: 2px;
  font-size: 1em;
  width: 100%;
  text-align: left;
  margin-top: -10px; }

.login .primary {
  background-color: #4CAF50;
  /* Green */
  border: 1px solid #d5d5d5;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem; }

.login .primary:hover {
  cursor: pointer;
  background-color: #5fdb63; }

.react-datepicker-wrapper {
  display: unset; }

.trip {
  display: -ms-flexbox;
  display: flex;
  clear: both;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  flex-direction: row;
  padding-top: 1.25em;
  padding: 1em;
  background-color: #272c33;
  margin-bottom: 1rem; }
  @media only screen and (max-width: 60em) {
    .trip {
      flex-direction: column; } }

.trip__item {
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-preferred-size: 90%;
  flex-basis: 90%; }

@media only screen and (max-width: 60em) {
  .destination .action {
    width: 10em; } }

.react-datepicker-popper {
  position: absolute;
  will-change: transform;
  top: 0px;
  left: 0px;
  transform: translate3d(677px, 301px, 0px); }

.counter {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  height: 8rem;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
  background-color: #272c33; }
  @media only screen and (max-width: 60em) {
    .counter {
      flex-direction: column; } }

.counter__type {
  min-width: 200px;
  height: 30px;
  padding: 2em;
  color: #f7f7f7;
  background-color: #676767;
  text-align: center;
  position: relative; }
  @media only screen and (max-width: 60em) {
    .counter__type {
      margin-top: 1em; } }

.counter__type__title {
  margin: 0;
  position: absolute;
  top: 30%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%); }

.counter__type__value {
  margin: 0;
  position: absolute;
  top: 80%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  font-size: 1.5rem; }

.counter__type--selected {
  color: #eee;
  background-color: #2998ff; }

.destination {
  height: 8rem;
  padding: 1rem;
  text-align: center;
  background-blend-mode: multiply;
  background-size: cover;
  background-color: #272c33; }

.destination input {
  float: left;
  padding: 12px;
  font-size: 1.15em;
  border: 1px solid #d5d5d5;
  width: 100%; }

@media only screen and (max-width: 60em) {
  .destination .field {
    height: 5em; } }

.destination .error {
  color: #A50000;
  padding: 2px;
  font-size: 1em;
  width: 100%;
  text-align: left;
  margin-top: -10px; }

.destination__form {
  padding-top: 1.5rem; }

.destination .primary {
  background-color: #4CAF50;
  /* Green */
  border: 1px solid #d5d5d5;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem; }

.destination .primary:hover {
  cursor: pointer;
  background-color: #5fdb63; }

.react-datepicker-wrapper {
  display: unset; }

.row {
  max-width: 114rem;
  margin: 0 auto;
  clear: both; }
  @media only screen and (max-width: 56.25em) {
    .row {
      max-width: 50rem;
      padding: 0 3rem; } }
  .row::after {
    content: "";
    display: table;
    clear: both; }

[class^="col-"] {
  float: left;
  padding-right: 20px; }
  @media only screen and (max-width: 56.25em) {
    [class^="col-"] {
      width: 100% !important; } }

[class*='col-']:last-of-type {
  padding-right: 0; }

.col-1-of-2 {
  width: calc((100% - 6rem) / 2); }

.col-1-of-3 {
  width: calc((100% - 2 * 6rem) / 3); }

.col-2-of-3 {
  width: calc(2 * ((100% - 2 * 6rem) / 3) + 6rem); }

.col-1-of-4 {
  width: calc((100% - 3 * 6rem) / 4); }

.col-2-of-4 {
  width: calc(2 * ((100% - 3 * 6rem) / 4) + 6rem); }

.col-3-of-4 {
  width: calc(3 * ((100% - 3 * 6rem) / 4) + 2 * 6rem); }

.col-4-of-4 {
  width: 100%; }

.col-md-10 {
  width: 100%; }

.col-md-9 {
  width: 90%; }

.col-md-8 {
  width: 80%; }

.col-md-7 {
  width: 70%; }

.col-md-5 {
  width: 50%; }

.col-md-4 {
  width: 40%; }

.col-md-3 {
  width: 30%; }

.col-md-2 {
  width: 20%; }

.col-md-1-3 {
  width: 33.33%; }

.col-md-offset-1 {
  margin-left: 10%; }

.pd-md-1 {
  padding: 1rem; }

.mb-md-1 {
  margin: 1rem; }

.header {
  height: 4rem;
  position: sticky;
  top: 0px;
  background-color: #f7f7f7;
  border-bottom: 1px solid #e3e3e3;
  z-index: 999; }
  .header__logo {
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    padding-left: 1rem; }
  .header__menu {
    align-items: center;
    display: flex;
    list-style: none;
    margin: 0; }
  .header__right {
    margin-left: auto; }
  .header__settings {
    margin-top: 0.7rem;
    margin-right: 1rem;
    padding-left: 1rem; }
